.App {
  font-family: Arial, sans-serif;
  color: #333;
}

header {
  background-color: #0044cc;
  color: white;
  padding: 1rem;
}

.hero {
  background: url('/public/image.jpg') no-repeat center center/cover;
  color: white;
  text-align: center;
  padding: 5rem 1rem;
}

section {
  padding: 3rem 1rem;
}

button {
  background-color: #0044cc;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #003399;
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
  }

  .hero {
    padding: 2rem 1rem;
  }
}
